.container-chat {
  margin-top: 5px;
  position: relative;
  border-radius: 9px 9px 9px 0px;
  background-color: silver;
  width: 50px;
  height: 30px;
}

.circle {
  position: absolute;
  display: inline-block;
  height: 5px;
  width: 5px;
  background-color: black;
  margin: 3px;
  border-radius: 50%;
  animation: wave 1.5s infinite ease-in-out;
  top: 10px;
}
.circle:first-child {
  left: 10px;
}
.circle:nth-child(2) {
  left: 20px;
  animation-delay: 0.1s;
}
.circle:last-child {
  left: 30px;
  animation-delay: 0.2s;
}

@keyframes wave {
  0%,
  50% {
    top: 10px;
  }
  25% {
    top: 0px;
  }
}

.container {
    height: auto;
}
.messenger {
  display: grid;
  width: 100%;
  height: 100vh;
  background: #eeeef1;

  grid-template-columns: 350px auto;
  grid-template-rows: 60px auto 60px;
  grid-column-gap: 1px;
  grid-row-gap: 1px;
}

.container {
  padding: 10px;
}

.scrollable {
  position: relative;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}

.sidebar {
  background: white;
  grid-row-start: 1;
  grid-row-end: span 3;
}

.content {
  background: white;
  grid-row-start: 1;
  grid-row-end: span 3;
}

.footer {
  grid-column-start: 2;
  background: white;
}
